import React from 'react';
import AnnounsmentItem from './AnnounsmentItem';


export const AnnounsmentList = ({announcements, onDeleted}) => {

    return (
        <div>

            <ul>
                {announcements.map(announcement =>
                    (<AnnounsmentItem
                        announcement={announcement}
                    onDeleted={() => onDeleted(announcement.title)}/>)
                )
                }
            </ul>

        </div>
    );

}
