import React, {Component} from 'react';
import './App.css';
import {Search} from './components/Search';
import Header from './components/Header';
import {Add} from './components/Add';
import {AnnounsmentList} from './components/AnnounsmentList';
import {Edit} from './components/Edit';

export default class App extends Component {
state = {
    announcements : [{
        title: '1',
        description: 'lorem10',
        date: '22.10.1985'
    },
        {
            title: '11',
            description: 'lorem101111',
            date: '22.09.1985'
        }]
}
delItem = (title) => {
    this.setState(
        ({announcements}) => {
        
            return{
                announcements: announcements.filter(a => a.title!==title),
            };

        }
    );
}
    render(){

        return (
            <>
                <div className="Header">
                    <Header />
                </div>
                <div className="Search">
                    <Search/>
                </div>
            <div className="App">
               <Add />
               <AnnounsmentList
                   announcements={this.state.announcements}
                   onDeleted={this.delItem}/>

                    <Edit />

            </div>
            </>
        );
    }}
